import React, { useState } from 'react';
import Button from 'react-bootstrap/Button';
import Alert from 'react-bootstrap/Alert';
import '../index.css'


const HookUseState = () => {
    const [count, setCount] = useState(10);
    const [error, setError] = useState(false);

    const ClickIncrease = () => {
        setCount(count => count + 1)
        setError(false)
    }
    const ClickReset = () => {
        setCount(0)
        setError(false)
    }

    if (count < 0) {
        setError(true);
        setCount(0);
    }
    else {
        return (
            <div className="Hookuse">
                {error == true ? <Alert variant="danger">Value of counter cannot be negative!</Alert> : null}
                <h3 className="HeadingOfHook">Value of Counter: {count}</h3><br />
                <Button variant="outline-dark" onClick={() => ClickIncrease()} block>Increase Counter Value</Button><br />
                <Button variant="outline-info" onClick={() => setCount(count - 1)} block>Decrease Counter Value</Button><br />
                <Button variant="outline-dark" onClick={() => ClickReset()} block>Set to 0</Button>
            </div>
        );
    }
};


export default HookUseState;